body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #dddddd;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    width: 500px;
    height: 500px;
}

hr {
  color: #dddddd;
}

.content {
    color: #282c34;
}

.spinner-specific {
    font-size: 2px;
    color: #999;
}

.spinner {
    font-size: 2px;
    color: #999;
    text-align: center;
}

.overlay-spinner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.7;
    background: aliceblue;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #000;
    font-size: 2px;
}

ul {
    list-style: none;
    padding: 0;
}

.map {
  height: 400px; }

.marker {
  background-image: url(/static/media/mapIcon.7c30ca39.svg);
  position: absolute;
  cursor: pointer;
  width: 49px;
  height: 64px;
  top: -64px;
  left: -24.5px;
  -webkit-transform-origin: 24.5px 64px;
          transform-origin: 24.5px 64px;
  margin: 0;
  padding: 0; }

.header {
  width: 100%;
  height: 70px;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 0 1px #eee; }
  .header img {
    height: 30px; }

.marker-container {
  text-align: left; }
  .marker-container .input-container label {
    font-size: .91rem; }
  .marker-container h2 {
    font-size: 1rem;
    font-weight: 600; }

.address-options li {
  font-size: .91rem;
  cursor: pointer;
  color: #777; }

.address-options li:hover {
  background: #f3f3f3; }

.cord-body label {
  font-size: .8rem; }

